@use '../../1-tools/scss/helpers';
@use 'settings/base';

@mixin colors-example {
  /* critical:start */
  .color-example {
    $styleguide-color-size: 8em;

    @include helpers.clearlist;

    display: flex;
    flex-wrap: wrap;
    flex-basis: $styleguide-color-size;
    justify-content: center;

    width: 100%;
    margin: 0;
    margin: 2rem 0;
    padding: 0;
    list-style: none;
    text-align: center;

    &.is-secondary {
      li {
        width: 25%;
        margin: 0;

        .color {
          width: 100%;
          height: 5em;
          margin: 0;
          border: 0;
          border-radius: 0;
          box-shadow: none;
        }
      }
    }

    li {
      position: relative;
      margin: 2rem 0;

      .color {
        display: block;
        width: $styleguide-color-size;
        height: $styleguide-color-size;
        margin: 0 2rem 0.4rem;
        border: 5px solid #fff;
        border-radius: $styleguide-color-size;
        box-shadow: 0 0 60px rgba(0, 0, 0, 0.1), 0 10px 20px -15px rgba(0, 0, 0, 0.7), 0 25px 25px -35px black;
      }

      .color-rgb,
      .color-hex {
        display: block;
        min-width: $styleguide-color-size;
        font-size: 0.65em;
        color: #8e8e8e;
        text-align: center;
      }

      .color-rgb {
        margin: 1em;
      }

      .color-hex {
        opacity: 0.6;
      }
    }
    // Color Brand
    .color-brand .color {
      background: base.$color-brand;
    }

    .color-brand-2 .color {
      background: base.$color-brand-2;
    }

    .color-white .color {
      background: base.$color-white;
    }

    .color-black .color {
      background: base.$color-black;
    }

    // COLOR BRAND

    .color-brand-175 .color {
      background: base.$color-brand-175;
    }

    .color-brand-150 .color {
      background: base.$color-brand-150;
    }

    .color-brand-125 .color {
      background: base.$color-brand-125;
    }

    .color-brand-110 .color {
      background: base.$color-brand-110;
    }

    .color-brand-75 .color {
      background: base.$color-brand-75;
    }

    .color-brand-50 .color {
      background: base.$color-brand-50;
    }

    .color-brand-25 .color {
      background: base.$color-brand-25;
    }

    .color-brand-10 .color {
      background: base.$color-brand-10;
    }


    // COLOR BLACK

    .color-black-75 .color {
      background: base.$color-black-75;
    }

    .color-black-50 .color {
      background: base.$color-black-50;
    }

    .color-black-25 .color {
      background: base.$color-black-25;
    }

    .color-black-15 .color {
      background: base.$color-black-15;
    }

    .color-black-10 .color {
      background: base.$color-black-10;
    }

    .color-black-7 .color {
      background: base.$color-black-7;
    }

    .color-black-5 .color {
      background: base.$color-black-5;
    }


    .color-success .color {
      background: base.$color-success;
    }

    .color-error .color {
      background: base.$color-error;
    }

    .color-info .color {
      background: base.$color-info;
    }
  }

  /* critical:end */
}
